#popup-politics {
    position: fixed;
    top: 0;
    width: 100%;    
    z-index: 20;
    background-color: #ff5592;
    transition: top .4s ease;

    &.form {
        background-image: linear-gradient(90deg, rgb(236, 112, 0), rgb(255, 85, 146));
    }
    
    &.hidden {
        top: -65px;
    }

    .ctrn {
        @include custom-container();
        padding-right: 30px;
        padding-left: 10px;
        height: 65px;
        display: flex;
        justify-content: center; 
        align-items: center;
        
        @include viewport(slarge){
            padding: 20px;
        }

        .privacity {
            margin: 0;
            font-family: ItauText;
            font-size: 11px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.39;
            letter-spacing: normal;
            text-align: left;
            color: #FFF;

            @include viewport(smedium){
                font-size: 13px;
            }

            @include viewport(slarge){
                font-size: 14px;
                justify-content: flex-start;
                text-align: left;
            }

            &__button {
                position: absolute;
                right: 5px; 
                top: 30%;
                color: #FFF;
                display: inline-block;
                background: transparent;
                border: transparent;
                font-size: 18px;
            }
        }

        .politics {
            color: #FFF;
            text-decoration: underline;
        }
    }
}

