#header {
    background: #fff;
    transition: all .4s ease;
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 21;
    box-shadow: 0 2px 100px 0 rgba(0, 0, 0, 0.11);

    &.popup-active {
        top: 65px;
    }

    .ctrn {
        position: relative;
        @include custom-container();
        @include flex-grid();
        @include flex-align(center, center);
        min-height: 60px;
        padding: 10px 15px; 

        @include viewport(xmedium){
            justify-content: space-between;
            padding: 0px 30px;
            min-height: 80px;
        }

        .nav {
            .dialogacessiblity {
                position: absolute;
                left: 50%;
                transform: translateX(-50%) scale(0);
                width: 200px;
                padding: 15px;
                background-image: linear-gradient(90deg,#ec7000,#ff5592);
                text-align: center;
                top: -1px;
                opacity: 0;
                transition: opacity .3s linear;
                color: #fff;

                &:nth-child(3) {
                    z-index: 1;
                }

                &:nth-child(2) {
                    z-index: 2;
                }

                &:nth-child(1) {
                    z-index: 3;
                }
            }

            &__btn {
                @include flex-grid(column);
                padding: 0;
                margin: 0;
                left: 30px;
                border: 0;
                @include box(28px, 28px);
                outline: none;
                position: absolute;
                background: transparent;
                top: 50%;
                transform: translateY(-50%);
    
                &__bar {
                    border-radius: 2px; 
                    display: block;
                    width: 100%;
                    height: 2px;
                    background: #605751;
                    margin-top: 6px;
                }

                @include viewport(xmedium){
                    display: none;
                }
            }
    
            &__logo {
                text-indent: -99999px;
                overflow: hidden;
                width: 48px;
                height: 38.5px;
                background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/logo-mob.svg) no-repeat center;
                background-size: contain;

                @include viewport(xmedium){
                    width: 58px;
                    height: 58px;
                    background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/logo.svg) no-repeat center;
                    background-size: contain;
                }

                &-mobile {
                    background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/logo.svg) no-repeat;
                    width: 100%;
                    max-width: 58px;
                    height: 68.5px;
                }
            }   
        }

        .menu {
            position: fixed;
            width: 75vw;
            height: 100vh; 
            padding: 25px; 
            top: 0;
            left: -75vw;
            transition: all .4s ease;

            &.active {
                left: 0px;
                z-index: 25;
            }
            
            background-color: #fff;

            &__nav {
                display: flex;
                justify-content: space-between;
            }

            &__copy {
                width: 186px;
                opacity: 0.95;
                font-family: ItauDisplay;
                font-size: 14px;
                color: #999999;
                position: absolute;
                bottom: 0;
                margin-bottom: 5px;
            }   

            &__btn {
                font-size: 42px;
                font-weight: 100;
                color: #ec7000;
                text-decoration: none;
            }
            
            &__logo {
                text-indent: -99999px;
                overflow: hidden;
                width: 30px;
                height: 28.5px;
                background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/logo-mob.svg) no-repeat center;
                background-size: contain;
                @include viewport(slarge){
                    width: 58px;
                    height: 58px;
                    background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/logo.svg) no-repeat center;
                    background-size: contain;
                }
            }   

            &__list {
                display: flex;
                flex-direction: column;
                height: calc(100% - 71px);
                justify-content: space-between;            

                ul { 
                    display: flex;
                    flex-direction: column;
                    align-content: flex-end;
                    list-style: none;
                    padding: 0;
                    margin-top: -20px;
                    
                    @include viewport(xmedium){
                        flex-direction: row;
                        position: absolute;
                    }
                    
        
                    h2 {
                        margin: 0px 0px 25px;   
                    }
        
                    li {
                        a {
                            font-family: ItauDisplay;
                            opacity: 0.95;
                            font-family: ItauDisplay;
                            font-size: 18px;
                            color: #232e3e;
    
                            &:focus {
                                text-decoration: underline;
                            }

                            @media(max-height: 400px){
                                font-size: 16px;
                            }
                        }
    
                        margin-bottom: 15px;

                        @media(max-height: 400px){
                            margin-bottom: 10px;
                        }
        
                        &:first-child {
                            margin-top: 45px;
    
                            @include viewport(xmedium){
                                margin: 0px;
                            }
                        }
                    }
                }

                ul.bottom {
                    margin-bottom: 0;

                    li {


                        a {
                            font-size: 16px;

                            @media(max-height: 400px){
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
        }

        .menu-desk {
            display: none;
            transition: all .4s ease;
            @include viewport(xmedium){
                display: block;
                margin-right: 0px;

                &.active {
                    margin-right: 280px;

                    @include viewport(xlarge){
                        margin-right: 250px;
                    }
                }
            
                ul {
                    list-style: none;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    margin: 0px;
                    padding: 0px;
    
                    li {
                        margin-left: 20px;  
                        a {
                            font-family: ItauDisplay;
                            opacity: 0.95;
                            font-family: ItauDisplay;
                            font-size: 16px;
                            color: #232e3e;

                            &:focus {
                                text-decoration: underline;
                            }

                            &.active {
                                color: #ff5592;
                            }
                        }
                    }
                }
            }
        }

        .button-nav {
            box-shadow: none;
            margin: 0;
            width: 248px;
            height: 48px;
            border-radius: 24px;
            position: absolute;
            background-image: linear-gradient(90deg,#ec7000,#ff5592);
            color: #FFF;
            right: -250px;
            opacity: 0;
            transition: all .4s ease;
            text-align: center;
            padding-top: 10px;
            pointer-events: none;
            text-decoration: none;

            &.active {
                opacity: 1;
                right: 30px;
                pointer-events: auto;

                @include viewport(xlarge){
                    right: 0px;
                }

                &:hover, &:focus {
                    opacity: 0.8;
                }

                &:active {
                    outline: none;
                }
            }
        }
    }
}

body {
    &.stop {
        overflow: hidden;
    }
}

.js-div {
    opacity: 0;
    transition: opacity .7s ease-in-out;

    &.backdrop {
        opacity: 0.5;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 20;
        width: 100vw;
        height: 100vh;
        background-color: #000;
        opacity: .5;
    }
}