#hero-dev {
    background-image: linear-gradient(90deg, #1b2534, #2b374a);


    .container {
        @include custom-container();
        padding: 18px 0px 83px 14px;

        @include viewport(smedium){
            padding: 18px 18px 40px;
            max-width: 584px;
        }

        @include viewport(xmedium){
            max-width: 816px;
            padding: 45px 24px 67px;
        }

        @include viewport(slarge){
            max-width: 1082px;
            padding: 45px 24px 54px;
        }

        @include viewport(xlarge){
            max-width: 1137px;
            padding: 41px 0 54px;
        }
        

        .controller {
            background: url(../img/dev/smart.png) no-repeat calc(50% - -45px) 30%, url(../img/dev/grafico.png) no-repeat calc(50% - -75px) 30%;
            
            @include viewport(xsmall){
                background: url(../img/dev/smart.png) no-repeat calc(50% - -45px) 30%, url(../img/dev/grafico.png) no-repeat calc(50% - -65px) 30%;
            }
            
            @include viewport(smedium){
                position: relative;
                background: url(../img/dev/smart@576.png) no-repeat calc(50% - -125px) 100%, url(../img/dev/grafico@576.png) no-repeat calc(50% - -145px) 100%;
            }

            @include viewport(xmedium){
                background: url(../img/dev/smart@768.png) no-repeat calc(50% - -105px) 100%,url(../img/dev/grafico@768.png) no-repeat calc(50% - -145px) 100%;
            }

            @include viewport(slarge){
                padding: 42px 0 49px;
                background: url(../img/dev/smart@992.png) no-repeat calc(50% - -105px) 100%,url(../img/dev/grafico@992.png) no-repeat calc(50% - -145px) 100%;
            }

            @include viewport(xlarge){
                padding: 95px 0 95px;
                background: url(../img/dev/smart@1440.png) no-repeat calc(100% - 116px) 90%,url(../img/dev/grafico@1440.png) no-repeat 100% 100%;
            }

            .wrapper {
                width: 100%;

                @include viewport(smedium){
                    width: 55%;
                }

                h1 {
                    font-family: ItauDisplay;
                    font-size: 28px;
                    font-weight: bold;
                    font-style: normal;
                    font-stretch: normal;   
                    line-height: 1.29;
                    letter-spacing: normal;
                    color: #ffffff;

                    padding-right: 47px;
                    margin: 0;

                    width: 100%;
                    max-width: 220px;
                    margin-bottom: 140px;
    
                    span {
                        display: block;
                        color: #ff5592;
                    }

                    @include viewport(smedium){
                        max-width: 280px;
                        padding-right: 0;
                        margin: 0;
                        margin-bottom: 24px;
                    }

                    @include viewport(xmedium){
                        font-size: 38px;
                        padding-top: 20px;
                    }

                    @include viewport(slarge){
                        font-size: 50px;    
                        margin-top: 35px;
                        max-width: 400px;
                        padding: 0;
                        margin-top: 0;
                    }
                }

                p {
                    font-family: ItauText;
                    font-size: 14px;
                    font-weight: normal;
                    font-style: normal;
                    font-stretch: normal;
                    line-height: 1.71;
                    letter-spacing: normal;
                    color: #ffffff;

                    width: 100%;
                    max-width: 85%;

                    @include viewport(smedium){
                        max-width: 267px;   
                        margin-bottom: 16px;
                    }
                    
                    @include viewport(xmedium){
                        font-size: 16px;
                        max-width: 282px;
                    }

                    @include viewport(slarge){
                        max-width: 352px;
                        margin-bottom: 40px;
                    }
                }

                a {
                    background-image: linear-gradient(to right, #ec7000, #ff5592);
                    width: 100%;
                    max-width: 163px;
                    height: 48px;
                    border-radius: 24px;

                    display: flex;
                    justify-content: center;
                    align-items: center;

                    font-family: ItauText;
                    font-size: 16px;
                    font-weight: bold;
                    font-style: normal;
                    font-stretch: normal;
                    line-height: 1.25;
                    letter-spacing: normal;
                    text-align: center;
                    color: #ffffff;
                    text-decoration: none;

                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }
    }
}