#not-supported {
    height: 100vh;
    background: url('../img/bg-footer-mb.svg') no-repeat bottom right;
    background-size: 95vw;

    @include viewport(xmedium){
        background-size: 60vw;
    }

    @include viewport(xlarge){
        background: url('../img/not-supported- bg.svg') no-repeat 100% 100%;
        background-size: 400px;
    }
    
    .container {
        @include custom-container();
        padding: 0;

        .support {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            height: 80vh;

            @include viewport(xlarge){
                height: 100vh;
            }

            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 4vh;
                top: 0;
                left: 0;
                background-image: linear-gradient(to right, #ec7000, #ff5592);
            }

            &__img {
                width: 8vh;
                margin-top: 7.43vh;

                @include viewport(xlarge){
                    width: 10vh;
                }

                @include viewport(xxlarge){
                    width: 72px;
                    height: 98;
                }

            }

            &__text {
                display: block;
                margin-top: 3vh;
                width: 80vw;

                @include viewport(xmedium){
                    width: 100%;
                    max-width: 620px;
                }

                h2 {
                    width: 100%;
                    // max-width: 253px;
                    font-family: ItauDisplay;
                    font-size: 7vw;
                    font-weight: bold;
                    line-height: 1.33;
                    letter-spacing: normal;
                    text-align: center;
                    color: #2b374a;
                    margin: 0px;
                    padding: 0px 30px;
                    
                    @include viewport(xmedium){
                        font-size: 6vw;
                    }

                    @include viewport(xlarge){
                        font-size: 48px;
                    }
                    
                }

                p {
                    font-family: ItauText;
                    font-size: 5vw;
                    font-weight: normal;
                    font-style: normal;
                    font-stretch: normal;
                    line-height: 1.5;
                    letter-spacing: normal;
                    text-align: center;
                    color: #6b6b6b;
                    margin-top: 2vh;
                    
                    @include viewport(xmedium){
                        font-size: 4vw;
                    }

                    @include viewport(xlarge){
                        font-size: 16px;
                    }
                }
            }

            &__browsers {
                display: block;
                margin: 0px;
                width: 100%;
                margin-top: 3vh;

                ul {
                    display: flex;
                    justify-content: space-around;
                    flex-direction: row;
                    list-style: none;
                    padding: 0;
                    margin: 0 auto;
                    max-width: 320px;

                    @include viewport(xmedium){
                        max-width: 80vw;
                    }

                    @include viewport(large){
                        max-width: 543px;
                    }
                    

                    li {
                        margin: 0;
                        
                        img {
                            width: 100%;
                            max-width: 48px;

                            @include viewport(xmedium){
                                max-width: 20vw;
                            }
                            
                            @include viewport(large){
                                max-width: 89px;
                            }
                        }

                        p {
                            font-family: ItauText;
                            font-size: 14px;
                            line-height: 1.86;
                            text-align: center;
                            color: #6b6b6b;

                            @include viewport(xmedium){
                                font-size: 2vw;
                            }

                            @include viewport(large){
                                font-size: 18px;
                            }
                        }
                    }
                }
            }
        }
    }
}