.content-page {
    position: relative;
    z-index: 1;
    background: #fff;
}

.margin-content {
    transition: all .4s ease;
    margin-top: 60px;

    @include viewport(xmedium){
        margin-top: 80px;
    }

    &.popup-active {
        margin-top: 125px;

        @include viewport(xmedium){
            margin-top: 140px;
        }
    }
}