.footer-compensar {
    position: absolute;
    @include viewport (xlarge) {
        height: 550px;
        width: 100%;
        z-index: 0;
    }
}

#footer {
    background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/bg-footer-mb.svg) no-repeat 100% 100%, #1b2534;
    background-size: contain;
    bottom: 0px;
    height: auto;
    left: 0;
    right: 0;
    z-index: 0;
    position: relative;

    @include viewport(xmedium){
        position: unset;
        bottom: 0;
    }

    @include viewport(smedium){
        background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/bg-footer-mb.svg) no-repeat bottom right, #1b2534;
        background-size: 254px 182px;
        height: auto;
    }

    @include viewport(xmedium){
        
        background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/bg-footer-desk.svg) no-repeat 102% 45%, #1b2534;
        background-size: 237px 264px;
        height: auto;
    }

    @include viewport(xlarge){
        position: fixed;
        background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/bg-footer-tablet.svg) no-repeat 102% 100%, #1b2534;
        background-size: 294px 471px;
        height: 550px;
    }

    .ctrn {
        padding: 55px 0 87vw;
        @include custom-container();

        @include viewport(smedium){
            padding: 50px 31px 5vw;
        }

        @include viewport(xmedium){
            padding: 50px 31px 0px;
        }

        @include viewport(slarge){
            padding: 50px 31px 0;
        }

        .logo {
            text-indent: -9999px;
            overflow: hidden;
            display: none;
            background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/logo-footer.svg) no-repeat center;
            background-size: contain;
            width: 68.6px;
            height: 69px;
            margin-bottom: 62px;
            cursor: pointer;

            @include viewport(smedium){
                display: inline-block;
            }
        }

        .controll {
            @include viewport(smedium){
                display: flex;
                position: relative;
            }

            @include viewport(xmedium){
                justify-content: flex-start;
            }

            .nav {
                position: relative;
                @include flex-grid(column);
                align-items: center;

                
                @include viewport(smedium){
                    flex-direction: row;
                    @include flex-align(center, flex-start);
                    align-items: flex-start;
                    margin-left: 15vw;
                    min-width: 130px;
                }

                @include viewport(slarge){
                    margin-left: 20vw;
                }

                @include viewport(xxlarge){
                    margin-left: 183px;
                }

                &__title {
                    font-family: ItauDisplay;
                    font-weight: bold;
                    font-size: 21px;
                    color: #ff5592;
                    margin-bottom: 16px;
                    text-decoration: none;

                    @include viewport(slarge){
                        font-size: 24px;
                    }
                }

                &__column {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    padding-left: 0;
                    margin-bottom: 40px;
    
                    @include viewport(smedium){
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                       
    
                        &:nth-child(2){
                            padding-left: 0px;
                            padding-top: 47px;
                            margin-left: 25px;
                        }
                    }

                    @include viewport(xmedium){
                        width: 100%;
                    }
    
                    &__title {
                        font-family: ItauDisplay;
                        font-weight: bold;
                        font-size: 21px;
                        color: #ff5592;
                        margin-bottom: 16px;
                        text-decoration: none;

                        @include viewport(slarge){
                            font-size: 24px;
                        }
                    }
    
                    &__item {
                        font-family: ItauDisplay;
                        font-size: 16px;
                        font-weight: normal;
                        font-style: normal;
                        font-stretch: normal;
                        line-height: normal;
                        letter-spacing: normal;
                        text-align: center;
                        color: $--white;
                        margin-bottom: 16px;
    
                        &:last-child {
                            margin-bottom: 0px;
                        }
    
                        @include viewport(smedium){
                            font-size: 16px;
                            margin-bottom: 24px;
                        }

                        @include viewport(slarge){
                            font-size: 16px;
                        }
                    }
                }
            }

            .contact {
                display: flex;
                align-items: center;
                flex-direction: column;
                margin-bottom: 40px;

                @include viewport(smedium){
                    margin-left: 8vw;
                    align-items: flex-start;
                }
                
                @include viewport(xxlarge){
                    margin-left: 210px;
                }
                
                &__title {
                    @include titulo-footer(#ff5592);
                    margin-bottom: 14px;
                    font-size: 21px;
                    font-weight: 700;

                    @include viewport(slarge){
                        font-size: 24px;
                    }
                }
    
                &__list {
                    list-style: none;
                    display: flex;
                    flex-direction: column;
                    padding: 0px;
                    margin: 0px;
                        
                    li {
                        margin-bottom: 12px;
                        @include texto1(#fff);
                        text-align: center;
                        font-size: 16px;
                        max-width: 280px;

                        @include viewport(smedium){
                            text-align: left;
                            max-width: unset;
                        }

                        @include viewport(slarge){
                            font-size: 16px;
                        }
    
                        &:last-child {
                            margin-bottom: 0px;
                        }
                    }
                    
                    a{  
                        display: block;
                        color: #fff;
                        text-decoration: none;
                    }

                }
            }
        }

        .social {
            @include viewport(smedium){
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                position: relative;
                bottom: 160px;
                width: 60%;
            }

            @include viewport(xmedium){
                width: 50%;
            }

            &__title {
               @include titulo-footer(#ff5592);
               margin-top: 14px;
               margin-bottom: 14px;
            }

            &__list {
                list-style: none;
                padding: 0;
                margin: 0;
                @include flex-grid();
                @include flex-align(center, center);
                
                @include viewport(smedium){
                    justify-content: center;
                }

                &__item{
                    &:nth-child(1){
                        margin-left: 0px;    
                    }

                    margin-left: 12px;
                    @include viewport(smedium){
                        &:nth-child(1){
                            margin-left: 0px;    
                        }
                        margin-left: 25px;
                    }


                    &__icon {
                        overflow: hidden;
                        text-indent: -9999999px;
                        display: block;
                        @include box(10vw);
                        background: #fff;
                        border-radius: 100%;

                        @include viewport(smedium){
                            width: 32px;
                            height: 32px;
                        }


                        &--facebook {
                            background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/icon-facebook.svg) no-repeat center, #fff;
                            background-size: 6vw;
                            @include viewport(smedium){
                                background-size: 16px;
                            }

                        }
                        &--instagram {
                            background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/icon-instagram.svg) no-repeat center, #fff;
                            background-size: 6vw;
                            @include viewport(smedium){
                                background-size: 16px;
                            }
                        }
                        &--youtube {
                            background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/icon-youtube.svg) no-repeat center, #fff;
                            background-size: 6vw;
                            @include viewport(smedium){
                                background-size: 16px;
                            }
                        }

                        &--twitter {
                            background: url(https://iti.itau/content/dam/iti-site-aberto/img/partials/icon-twitter.svg) no-repeat center, #fff;
                            background-size: 6vw;
                            @include viewport(smedium){
                                background-size: 16px;
                            }
                        }
                    }
                }
            }

            &__tj {
                font-family: ItauDisplay;
                font-size: 14px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                text-align: center;
                font-size: 14px;
                color: #6b6b6b;
                margin-top: 57px;

                @include viewport(smedium){
                    margin-top: 14px;
                }
            }
        }
    }
}