@mixin titulo1($color: $--dark){
    width: 100%;
    font-family: ItauDisplay, Arial, Helvetica, sans-serif;
    @include font-size(31px);
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: $color;
    @include viewport(xmedium){
        @include font-size(34px);
    }
    @include viewport(slarge){
        margin-bottom: 20px;
        font-size: 43px;
    }
    @include viewport(large){
        font-size: 48px;
    }
}

@mixin titulo2($color: $--brownish-grey) {
    width: 100%;
    font-family: ItauDisplay, Arial, Helvetica, sans-serif;
    @include font-size(37px);
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.15;
    letter-spacing: 1.3px;
    text-align: center;
    color: $color;
    @include viewport(slarge){
        font-size: 40px;
    }
}
@mixin titulo3($color: $--dark) {
    width: 100%;
    font-family: ItauDisplay;
    font-size: 24px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $color;
    @include viewport(slarge){
        font-size: 32px;
    }
}


@mixin titulo-footer($color: $--white){
    font-family: ItauDisplay;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $color;
    margin-bottom: 40px;
}


@mixin texto1($color: $--brownish-grey) {
    width: 100%;
    font-family: ItauText, Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: $color;
}


@mixin texto2($color: $--white) {
    width: 100%;
    font-family: ItauText, Arial, Helvetica, sans-serif;
    font-size: 24px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.33;
    letter-spacing: normal;
    text-align: center;
    color: $color;
}

@mixin texto3($color: $--white) {
    width: 100%;
    font-family: ItauDisplay, Arial, Helvetica, sans-serif;
    opacity: 0.97;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.9px;
    text-align: center;
    color: $color;
}

@mixin  texto4($color: $--brownish-grey) {
    font-family: ItauDisplay;
  font-size: 20px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.15;
  letter-spacing: 1.3px;
  text-align: center;
  color: $--brownish-grey;
}